<template>
  <div class="mycard">
    <header>
      <el-popover
        placement="right"
        width="300"
        trigger="click"
      >
        <div class="friendInfo">
          <div
            class="esInfo"
            v-if="user.user_info"
          >
            <div class="left">
              <div class="people">
                <div class="nickname">{{ user.user_info.nickname }}</div>
                <div class="nicknameEdit" 
                @click="editNickname()"
                style="padding:5px 5px;background-color: #007AFF;color:#fff;border-radius: 5px;width:70px;text-align: center;cursor: pointer;">
                  修改昵称</div>
                <div :class="[user.user_info.sex===1?'gender-male':'gender-female']"></div>
              </div>
              <div class="signature">{{ user.user_info.signature }}</div>
            </div>
            <div class="right" style="position: relative;">
              <img
                class="avatar"
                width="60"
                height="60"
                :src="user.img"
              >
              <input
                id="upload_file"
                type="file"
                placeholder="更换"
                accept=".jpeg,.jpg,.png,.gif"
                class="upload-file"
                @change="fileChange($event)"
              />
            </div>
          </div>
          <div
            v-if="user.user_info"
            class="detInfo"
          >
            <div class="list_id"><span>微信号</span><span>{{ user.user_info.username }}</span></div>
            <!-- <div class="list_id"><span>微信号</span><span>{{ user.user_info.wx }}</span></div> -->
          </div>
        </div>
        
        <img
          slot="reference"
          :src="user.img"
          class="avatar"/>
      </el-popover>
    </header>
    <div
      class="navbar"
      @click="clearSearch"
    >
      <p
        v-if="unreadsum > 0"
        class="unread"
      >{{ unreadsum }}</p>
      <p
        v-if="user.new_friend_tips_num > 0"
        class="unread newFriend"
      >{{ user.new_friend_tips_num }}</p>
      <router-link
        to="/chat"
        class="icon iconfont icon-msg"
      ></router-link>
      <router-link
        to="/friend"
        class="icon iconfont icon-friend"
      ></router-link>
      <router-link
        to="/my"
        class="icon iconfont icon-collection"
      ></router-link>
    </div>
    <footer>
      <el-dropdown
        placement="top"
        @command="handleCommand"
      >
        <i
          class="icon iconfont icon-more"
          style="margin-top: 0;"
        ></i>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="logout">退出登录</el-dropdown-item>
          <el-dropdown-item command="reset">修改密码</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </footer>
    <el-dialog
      title="密码修改"
      :visible.sync="dialogVisible"
      width="500px"
      :close-on-click-modal="false"
    >
      <el-form :model="formData">
        <el-form-item
          label="输入原密码"
          label-width="60"
        >
          <el-input
            v-model="formData.pass1"
            autocomplete="off"
            show-password
          ></el-input>
        </el-form-item>
        <el-form-item
          label="输入新密码"
          label-width="60"
        >
          <el-input
            v-model="formData.pass2"
            autocomplete="off"
            show-password
          ></el-input>
        </el-form-item>
        <el-form-item
          label="确认新密码"
          label-width="60"
        >
          <el-input
            v-model="formData.pass3"
            autocomplete="off"
            show-password
          ></el-input>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button
          type="primary"
          @click="handleReset"
        >确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="昵称修改"
      :visible.sync="dialogNicknameVisible"
      width="300px"
      :close-on-click-modal="false"
    >
      <el-form :model="formData2">
        <el-form-item
          label="请输入昵称"
          label-width="60"
        >
          <el-input
            v-model="formData2.nickname"
            autocomplete="off"
          ></el-input>
        </el-form-item>
       
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogNicknameVisible = false">取 消</el-button>
        <el-button
          type="primary"
          @click="handleSaveNickname"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { clearStore } from '@/utils/store'
import { resetPassword,resetNickname,fetchUserInfo,chgAvatar } from '@/api'
import store from '../../store'
const { photoUrl } = window.__gconf
export default {
  data () {
    return {
      formData: {
        pass1: '',
        pass2: '',
        pass3: ''
      },
      formData2:{
        nickname:'',
      },
      hasToken: false,
      dialogVisible: false,
      dialogNicknameVisible:false
    }
  },
  computed: {
    ...mapState([
      'user',
      'unreadsum'
    ])
  },
  mounted () {
    console.log(this.user)
  },
  methods: {
    clearSearch () {
      this.$store.dispatch('search', '')
    },
    handleCommand (command) {
      if (command == 'reset') {
        this.dialogVisible = true
      } else {
        clearStore()
        this.$router.push('/login')
      }
    },
    editNickname(){
      this.dialogNicknameVisible = true;
    },
    handleReset () {
      if (!this.formData.pass1 || this.formData.pass1.length < 6) {
        this.$message.error('请输入原密码,不能小于6位')
        return;
      }
      if (!this.formData.pass2 || this.formData.pass2.length < 6) {
        this.$message.error('请输入新密码,不能小于6位')
        return;
      }
      if (!this.formData.pass3 || this.formData.pass3.length < 6) {
        this.$message.error('请确认新密码,不能小于6位')
        return;
      }
      if (this.formData.pass2 !== this.formData.pass3) {
        this.$message.error('两次新密码不一致')
        return;
      }
      resetPassword(this.formData).then(res => {
        this.$message.success('已修改,请重新登陆')
        setTimeout(() => {
          this.handleCommand()
        }, 1000);
      })
    },
    handleSaveNickname(){
      if (!this.formData2.nickname) {
        this.$message.error('请输入昵称')
        return;
      }
      resetNickname({nickname:this.formData2.nickname}).then(res => {
        this.$message.success('修改成功')
        
        //刷新数据
        fetchUserInfo().then(res => {
          const userInfo = res.data.user_info
          store.dispatch('updataUser', {
            ...res.data,
            name: userInfo.username,
            img: photoUrl + userInfo.photo,
            uid: userInfo.id
          })
          
        })
        this.user.user_info.nickname = this.formData2.nickname;
        setTimeout(() => {
          this.dialogNicknameVisible =  false;
        }, 1000);
      })
    },
    fileChange (e) {
      let _this = this
      let len = e.target.files.length;
      for (var i = 0; i < len; i++) {
        let url = window.URL || window.webkitURL
        let img = new Image();
        let item = e.target.files[i]
        img.src = url.createObjectURL(item)

        img.onload = function () {
          console.log(item);
          chgAvatar({
            _token: localStorage.getItem('token'),
            file: item
          }).then(res => {
            if (res.err == 0) {
              var url = res.data.save_name;
              _this.user.img = photoUrl + url;
              _this.user.user_info.photo = url;
              //刷新数据
              fetchUserInfo().then(res => {
                const userInfo = res.data.user_info
                store.dispatch('updataUser', {
                  ...res.data,
                  name: userInfo.username,
                  img: photoUrl + userInfo.photo,
                  uid: userInfo.id
                })
              })
            }

          })
        }
      }

    },
  }
}
</script>

